import React, {useState} from 'react';
import style from './ProfileInfo.module.css';
import userPhoto from '../../../assets/images/userPhoto.png';
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";

const ProfileInfo = ({profile, status, updateStatus, isOwner, savePhoto}) => {
    let [editMode, setEditMode] = useState(false);

    const onMainPhotoSelected = (e) => {
        if(e.target.files.length) {
            savePhoto(e.target.files[0])
        }
    };

    return (
        <div className={style.info}>
            <img src={profile.photos.large ? profile.photos.large : userPhoto} className={style.picture} alt={'userPhoto'}/>
            {isOwner && <input type={'file'} onChange={onMainPhotoSelected}/>}

            {editMode ? <ProfileDataForm profile={profile}/> : <ProfileData profile={profile} />}

            <div><ProfileStatusWithHooks status={status} updateStatus={updateStatus}/></div>
        </div>
    )
};
const ProfileData = ({profile}) => {
    return (
        <div>
            <div className={style.nickName}>
                {profile.fullName}
            </div>
            <div>
                <b>About me</b>: {profile.aboutMe ? profile.aboutMe : ' '}
            </div>
            <div>
                <b>Looking for a job</b>: {profile.lookingForAJob ? 'yes' : 'no'}
            </div>
            {profile.lookingForAJob &&
            <div>
                <b>My profesional skills</b>: {profile.lookingForAJobDescription}
            </div>}
            <div>
                <b>Contacts</b>: {Object.keys(profile.contacts).map(key => {
                return <Contact key={key} contactTitle={key} contactValue={profile.contacts[key]}/>
            })}
            </div>
        </div>
    )
};
const ProfileDataForm = ({profile}) => {
    return (
        <div>
            <div className={style.nickName}>
                {profile.fullName}
            </div>
            <div>
                <b>About me</b>: {profile.aboutMe ? profile.aboutMe : ' '}
            </div>
            <div>
                <b>Looking for a job</b>: {profile.lookingForAJob ? 'yes' : 'no'}
            </div>
            {profile.lookingForAJob &&
            <div>
                <b>My profesional skills</b>: {profile.lookingForAJobDescription}
            </div>}
            <div>
                <b>Contacts</b>: {Object.keys(profile.contacts).map(key => {
                return <Contact key={key} contactTitle={key} contactValue={profile.contacts[key]}/>
            })}
            </div>
        </div>
    )
};

const Contact = ({contactTitle, contactValue}) => {
    return (
        <div className={style.contact}>
            <b>{contactTitle}</b>: {contactValue}
        </div>
    )
};

export default ProfileInfo;